import React from 'react';
import { useSelector } from 'react-redux';
import order_detals from './OrderDetails.module.css';
import icon from '../../image/icon.svg';

export default function OrderDetails() {

    const orderNumber = useSelector(state => state.orderState.orderNumber);

    return(
            <div className={order_detals.box} >
                <h2 className={order_detals.title}>{orderNumber}</h2>
                <h3 className={order_detals.subtitle}> </h3>
                <img src={icon} alt=""/>
                <p className={order_detals.text}>   </p>
                <p className={order_detals.text_purple}>    </p>
            </div>
    );
};