import {getIngredients, useIngredients} from '../../utils/IngredientsApi'

//   
export const GET_INGREDIENTS_REQUEST = 'GET_INGREDIENTS_REQUEST';//   
export const GET_INGREDIENTS_SUCCESS = 'GET_INGREDIENTS_SUCCESS';// 
export const GET_INGREDIENTS_ERROR = 'GET_INGREDIENTS_ERROR';// 

//  
export const SELECT_INGREDIENT = 'SELECT_INGREDIENT';
export const DELETE_SELECTED_INGREDIENT = 'DELETE_SELECTED_INGREDIENT_DATA';

//    
export const GET_ORDER_NUMBER_REQUEST = 'GET_ORDER_NUMBER_REQUEST';
export const GET_ORDER_NUMBER_SUCCESS = 'GET_ORDER_NUMBER_SUCCESS';
export const GET_ORDER_NUMBER_ERROR = 'GET_ORDER_NUMBER_ERROR';
export const DELETE_ORDER_NUMBER = 'DELETE_ORDER_DATA';//  

//   
export const BURGER_INGREDIENT = 'BURGER_INGREDIENT';
export const CLEAR_INGREDIENT_ORDER = 'CLEAR_INGREDIENT_ORDER'
export const DELETE_BURGER_INGREDIENT = 'DELETE_BURGER_INGREDIENT'
export const MOVE_CONSTRUCTOR_INGREDIENTS = 'MOVE_CONSTRUCTOR_INGREDIENTS'

//  
export const INGREDIENT_WINDOW_OPEN = 'INGREDIENT_WINDOW'
export const INGREDIENT_WINDOW_CLOSE = 'INGREDIENT_WINDOW_CLOSE'

export function onGetIngredients() {
    return (dispatch) => {
        dispatch({
        type: GET_INGREDIENTS_REQUEST,
    });
    getIngredients()
    .then((res) => {
            dispatch({
            type: GET_INGREDIENTS_SUCCESS,
            payload: {
                ingredients: res.data,
            },
            });
        })
        .catch((err) => {
            console.log(`! ${err}`);
            dispatch({
                type: GET_INGREDIENTS_ERROR,
            });
        }) 
    };
}

export function getOrderNumber(ingredientIds) {
    return (dispatch) => {
        dispatch({
            type: GET_ORDER_NUMBER_REQUEST,
        });
        useIngredients(ingredientIds)
        .then((res) => {
                dispatch({
                    type: GET_ORDER_NUMBER_SUCCESS,
                    payload: {
                    order: res.order.number,
                    },
                });
        })
        .catch((err) => {
            console.log(`! ${err}`);
                dispatch({
                    type: GET_ORDER_NUMBER_ERROR,
                });
            }) 
    };
}